<template>
	<div id="view-employee" class="">
		<h3>Details</h3>
		<ul class="collection with-header">
			<li class="collection-header"><h4>{{name}}</h4></li>
			<li class="collection-item">Employee ID: <div class="chip">{{employee_id}}</div></li>
			<li class="collection-item">Department: <div class="chip">{{dept}}</div></li>
			<li class="collection-item">Position: <div class="chip">{{position}}</div></li>
		</ul>
		<router-link v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}" class="btn grey">Update</router-link>
		<button @click="deleteEmployee" class="btn red">Delete</button>
		<div class="fixed-action-btn">
			<router-link to="/" class="btn-floating btn-large green">Back</router-link>
		</div>
	</div>
</template>

<script>
	import db from './firebaseinit'
	export default {
		name: 'view-employee',
		data () {
			return {
				employee_id: null,
				name: null,
				dept: null,
				position: null
			}
		},
		beforeRouteEnter (to, from, next) {
			db.collection('employees').where('employee_id',
				'==', to.params.employee_id).get()
				.then(querySnapshot => {
					querySnapshot.forEach(doc => {
						next(vm => {
							vm.employee_id = doc.data().employee_id
							vm.name = doc.data().name
							vm.dept = doc.data().dept
							vm.position = doc.data().position
						})
					})
			})
		
		},
		watch: {
			'$route': 'fetchData'
		},
		methods: {
			fetchData () {
				db.collection('employees').where
				('employee_id', '==',
					this.$route.params.employee_id).get()
					.then(querySnapshot => {
						querySnapshot.forEach(doc => {
							this.employee_id = doc.data().employee_id
							this.name = doc.data().name
							this.dept = doc.data().dept
							this.position = doc.data().position
						})
					})
			},
			deleteEmployee () {
				if(confirm('Are you sure?')) {
					db.collection('employees').where
					('employee_id', '==',
						this.$route.params.employee_id).get()
						.then(querySnapshot => {
							querySnapshot.forEach(doc => {
								doc.ref.delete()
								this.$router.push('/')
							})
						})
				}
			}
		}
		
	}
</script>